        background-repeat: no-repeat;
        background-size: 110px, 90px, 85px, 80px, 60px, auto, 40px, 70px, 40px, 50px, 55px, 80px, 60px, 100px, 110px, auto;
        background-position: 5% 95%, 35% 29%, 18% 75%, 10% 65%, 15% 50%, 18% 10%, -1% 8%, 5% 40%, 85% 20%, 95% 10%, 75% 35%, 90% 50%, 95% 38%, 101% 70%, 90% 85%, 10% 25%;

